<template>
  <div id="message">
    <div class="tableHeader">
      <p>创建日期</p>
      <p>用户</p>
      <p>身份</p>
      <p>版本</p>
      <p>申请理由</p>
      <p>状态</p>
      <p>审核</p>
    </div>
    <div class="divideLine"></div>
    <Scroll class="content2" :probe-type="3" ref="scroll">
      <order-list :list="listShow"></order-list>
      <div class="paginationZone" v-show="listShow.length !== 0">
        <a-pagination
          :total="listAll.length"
          :show-total="
            (total, range) => `${range[0]}-${range[1]} of ${total} items`
          "
          :page-size="pageSize"
          :default-current="1"
          v-model:current="currentPage"
        />
      </div>
      <empty-data-placeholder
        v-if="listShow.length === 0"
        description="暂无工单数据"
      ></empty-data-placeholder>
    </Scroll>

    <div class="right-absolute-flex" v-show="isShowRightCoverZone">
      <review-comment-dialog
        @cancel="cancelClick"
        @ensure="ensureClick"
        ref="inputReasonMessage"
      ></review-comment-dialog>
    </div>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>

    <div class="absoluteScrollDialogStore">
      <div
        class="itemcontent"
        style="
          background-color: #000;
          width: 0px;
          height: 0px;
          margin-left: 10px;
          margin-bottom: 3px;
        "
      >
        <div
          class="details"
          v-show="viewBoardVisible"
          style="
            width: 0px;
            height: 0px;
            background-color: #fff;
            box-shadow: var(--color-text-grey) 0 0 7px;
            overflow: hidden;
            position: absolute;
            z-index: 9;
          "
        >
          <p class="imageZone" @click="hideViewDialog()"></p>
          <Scroll ref="roll" class="content3">
            <product-ticket-content
              :ticketContent="activeViewItem"
            ></product-ticket-content>
          </Scroll>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Scroll from "../../../components/common/scroll/Scroll.vue";
import {
  $confirmAuditTicket,
  $declineAuditTicket,
  $getFilterTicketAudit,
} from "../../../network/auditSystem/common";
import $ from "jquery";
import OrderList from "../../../components/content/auditSystem/orderList/OrderList.vue";
import ReviewCommentDialog from "../../../components/content/auditSystem/reviewCommentDialog/ReviewCommentDialog.vue";
import ProductTicketContent from "./childComponents/productTicketContent.vue";
import EmptyDataPlaceholder from "@/pages/internal/components/common/emptyDataPlaceholder/EmptyDataPlaceholder";

export default {
  name: "e-message",
  data() {
    return {
      isLoading: false,
      listAll: [],
      listShow: [],
      activeViewItem: [],
      isShowCoverPage: false,
      viewBoardVisible: false,
      isShowRightCoverZone: false,
      activeRecordIndex: 0,
      pageSize: 8,
      currentPage: 1,
      totalPage: 0,
    };
  },
  beforeRouteEnter(to, from, next) {
    if (window.localStorage.getItem("isStaff") === "true") {
      window.localStorage.setItem("currentIndex010003", 10);
      next();
    } else {
      next({
        path: "/tools/login",
      });
      window.localStorage.removeItem("list");
      window.localStorage.removeItem("order");
      window.localStorage.removeItem("currentIndex010003");
      window.localStorage.removeItem("userID");
      window.localStorage.removeItem("username");
      window.localStorage.removeItem("authToken");
      window.localStorage.removeItem("isStaff");
      window.localStorage.removeItem("isSuperUser");
      window.localStorage.removeItem("isProvider");
    }
  },
  async mounted() {
    let data = {
      work_line_id: 1900,
      //  "work_operation": 4300,
      //  "status": 3200,
      // "start_date": "2021-12",
      // "end_date": "2022-02"
    };
    this.isLoading = true;
    try {
      const response = await $getFilterTicketAudit(data);
      console.log(response);
      const { Code, msg } = response;
      // 获取当前登录用户所有工单数据
      if (Code === 200 && msg === "success") {
        console.log(response.details.tickets);
        this.listAll = response.details.tickets.reverse(); // 将数据赋值给listAll
        this.totalPage = Math.ceil(this.listAll.length / this.pageSize); // 重新计算totalPage
        const tempListAll = window.JSON.parse(
          window.JSON.stringify(this.listAll)
        );
        // 若listShow数组为空，则说明是首次加载组件
        if (this.listShow.length === 0) {
          this.listShow = tempListAll.slice(0, this.pageSize);
        }
        // 若进来页面时分页的currentPage和分页的totalPage相等，因为listAll的数据有可能进行了更新，所以需要更新listShow
        if (this.currentPage === this.totalPage) {
          this.listShow = tempListAll.slice(
            this.pageSize * (this.currentPage - 1),
            this.pageSize * this.currentPage
          );
        }
      } else {
        this.$toast.error("获取工单数据失败 ", 240);
        console.log("获取工单数据失败 ");
      }
    } catch (err) {
      this.$toast.error("获取工单数据失败  ", 240);
      console.log("获取工单数据失败 ");
    }
    this.isLoading = false;

    this.$bus.$on("viewDetailsClick", this.viewDetailsClick);
    this.$bus.$on("passClick", this.passClick);
    this.$bus.$on("passProduct", this.passProduct);
    this.$bus.$on("rejectClick", this.rejectClick);
  },
  unmounted() {
    this.$bus.$off("viewDetailsClick", this.viewDetailsClick);
    this.$bus.$off("passClick", this.passClick);
    this.$bus.$off("passProduct", this.passProduct);
    this.$bus.$off("rejectClick", this.rejectClick);
  },
  watch: {
    currentPage(newVal) {
      const tempListAll = window.JSON.parse(
        window.JSON.stringify(this.listAll)
      );
      this.listShow = tempListAll.slice(
        this.pageSize * (newVal - 1),
        this.pageSize * newVal
      );
      this.$refs.scroll.scrollTo(0, 0, 400);
      setTimeout(() => {
        this.$refs.scroll.refresh();
      }, 200);
    },
  },
  methods: {
    passClick(index) {
      // console.log(index);
      this.activeRecordIndex = index;
      this.$refs.inputReasonMessage.word = "通过";
      this.$refs.inputReasonMessage.message = "";
      this.isShowRightCoverZone = true;
      this.isShowCoverPage = true;
    },
    passProduct(itemId) {
      console.log("product passClick item id : ", itemId);
      this.activeRecordId = itemId;
    },
    viewDetailsClick(param) {
      const { x, y, index } = param;

      this.x = x;
      this.y = y;
      const elex = this.x;
      const eley = this.y;
      // this.activeViewItem = this.simpleProcessPresentInfo(this.listShow[index].content);
      this.activeViewItem = this.listShow[index].content;

      const vm = this;
      const str = ".details";
      this.isShowCoverPage = true;
      // 计算第二个动画要移动到哪个点
      let targetLeft;
      const clientWidth = document.body.clientWidth;
      if (clientWidth < 1000) {
        targetLeft = 0;
      } else if (clientWidth > 1000 && clientWidth < 1240) {
        targetLeft = (clientWidth - 1000) / 2;
      } else {
        targetLeft = 240 + (clientWidth - 240 - 1000) / 2;
      }

      let targetTop;
      let clientHeight = document.body.clientHeight;
      if (clientHeight < 680) {
        targetTop = 0;
      } else {
        targetTop = (clientHeight - 680) / 2;
      }

      $(str).animate({ left: elex, top: eley }, 50, function () {
        vm.viewBoardVisible = true;
      });
      $(str).animate(
        { left: targetLeft, top: targetTop, width: "100px", height: "100px" },
        100,
        function () {
          vm.$refs.roll.refresh();
          vm.$refs.roll.scrollTo(0, 0, 0);
        }
      );
      $(str).animate({ width: "1000px", height: "680px" }, 150);
    },
    rejectClick(index) {
      // console.log(index);
      this.activeRecordIndex = index;
      this.$refs.inputReasonMessage.word = "拒绝";
      this.$refs.inputReasonMessage.message = "";
      this.isShowRightCoverZone = true;
      this.isShowCoverPage = true;
    },
    hideViewDialog() {
      const str = ".details";
      const vm = this;
      $(str).animate({ width: "70px", height: "70px" }, 50);
      $(str).animate(
        { left: vm.x, top: vm.y, width: "0px", height: "0px" },
        200,
        function () {
          vm.isShowCoverPage = false;
          vm.viewBoardVisible = false;
        }
      );
    },
    cancelClick() {
      this.isShowCoverPage = false;
      this.isShowRightCoverZone = false;
    },
    async ensureClick(message) {
      // this.listAll[this.activeRecordIndex].id
      if (message === "") {
        this.$toast.warn("编辑理由不能为空", 240);
        return;
      }
      this.$refs.inputReasonMessage.ifShowLoadingGif = true;

      console.log(this.$refs.inputReasonMessage.word);
      if (this.$refs.inputReasonMessage.word === "通过") {
        try {
          console.log("通过id和review comment");
          console.log(this.listAll[this.activeRecordIndex].id, message);
          const response = await $confirmAuditTicket({
            // id: this.listAll[this.activeRecordIndex].id,
            id: this.activeRecordId,
            review_comment: message,
          });
          if (response.Code == 200) {
            this.$toast.success("操作成功", 240);
            this.$router.go(0);
          } else {
            this.$toast.error("操作失败", 240);
            this.$toast.error(response.Msg, 240);
          }
          // console.log(response);
        } catch (err) {
          this.$toast.error("操作失败", 240);
        }

        this.$refs.inputReasonMessage.ifShowLoadingGif = false;
        this.isShowCoverPage = false;
        this.isShowRightCoverZone = false;
      }
      if (this.$refs.inputReasonMessage.word === "拒绝") {
        try {
          console.log("通过id和review comment");
          console.log(this.listAll[this.activeRecordIndex].id, message);
          const response = await $declineAuditTicket({
            // id: this.listAll[this.activeRecordIndex].id,
            id: this.activeRecordId,
            review_comment: message,
          });
          if (response.Code == 200) {
            this.$toast.success("操作成功", 240);
            this.$router.go(0);
          } else {
            this.$toast.error("操作失败", 240);
            this.$toast.error(response.Msg, 240);
          }
        } catch (err) {
          this.$toast.error("操作失败", 240);
        }

        this.$refs.inputReasonMessage.ifShowLoadingGif = false;
        this.isShowCoverPage = false;
        this.isShowRightCoverZone = false;
      }
    },
  },
  components: {
    Scroll,
    OrderList,
    ReviewCommentDialog,
    ProductTicketContent,
    EmptyDataPlaceholder,
  },
};
</script>

